import React, { Fragment, useEffect, useRef , useState}  from 'react';
import Moosix from '../functions/mossix-api'
import MusicPreview from "../widgets/musicPreview";
let sliderVal1 = 0;



function Popular(props){
    const app = new Moosix()
    const [popular, setPopular] = useState([]);
    const [slider1, setSlider1] = useState(sliderVal1)
 
    useEffect(()=>{
        app.getPopular().then(data=>setPopular(data))
    }, [])

    function play(src){
        props.play(src)
    }

    function stop(){
        props.stop()
    }

    return(
        <div id="popular-container" className="home-music-container ">
            <div className="musics-texts">
                <div className="musics-title">
                    <h4>Popular musics</h4>
                </div>
                <div className="musics-subtext">
                    <span>Most popular musics for you</span>
                </div>
            </div>

            <div className = "line" />

            <div className="preview-box-container"  style = {{ transform: `translate(${slider1}px, ${85}px)` }}>
                {popular &&
                popular.map((name, i) => {
                    return (
                    <Fragment key={i}>
                        <MusicPreview
                        name={name}
                        play={play}
                        userName={props.userName}
                        stop = {stop}
                        audio = {props.audio}
                        />
                    </Fragment>
                    );
                })}
            </div>

            <div className = "content-slider">
                <div  id = "next" className = "slider" onClick = {()=>{
                    sliderVal1 -= 225;
                    setSlider1(sliderVal1) 
                    }}>
                    <span className="material-icons">
                        keyboard_arrow_right
                    </span>
                </div>
                <div id = "previous" className = "slider" onClick = {()=>{
                    sliderVal1= sliderVal1 + 225;
                    setSlider1(sliderVal1) 
                }}>
                    <span className="material-icons">
                        keyboard_arrow_left
                    </span>
                </div>
            </div>
        </div>
)

}
export default Popular