import React from "react";
import { NavLink, useHistory } from "react-router-dom";
import firebase from "firebase"
//import { Route, BrowserRouter as Router } from 'react-router-dom';
function Header() {
    const history = useHistory()
    let user1;
    firebase.auth().onAuthStateChanged((user) => {
        user1 = user
    })
    function signOut() {
        if (user1) {
            console.log("yes")
            firebase.auth().signOut()
            history.push("/login")
        } else {
            console.log("signout")
            history.push("/login")
        }
    }
    return (
        <div id="nav">
            <nav>
                <ul>
                    <NavLink to="/" exact activeClassName="btn-active">
                        <li className="btn">
                            <span class="material-icons">
                                home
                            </span>
                        </li>
                    </NavLink>

                    <NavLink to="/explore">
                        <li className="btn" >
                            <span class="material-icons">
                                explore
                                </span>
                        </li>
                    </NavLink>
                    <NavLink to="/upload" exact activeClassName="btn-active">
                        <li className="btn" >
                            <span class="material-icons">
                                upload
                                </span>
                        </li>
                    </NavLink>
                    <NavLink to="/about" exact activeClassName="btn-active">
                        <li className="btn" id="ab">
                            <span class="material-icons">
                                priority_high
                                </span>
                        </li>
                    </NavLink>
                    <div className="signout" onClick={signOut}>
                        <span className="material-icons">
                            login
                                </span>
                    </div>
                </ul>
            </nav>
        </div>
    )
}
export default Header