import React, { useEffect, useState } from "react";
import firebase from "firebase";
import dayFunc from "./functions/dayFunc.js";
import { Route, BrowserRouter as Router, Switch, useHistory, Redirect } from 'react-router-dom';
import Header from "./header"
import Nav from "./nav"

let image;
let file
let history;
var dateObj = new Date();
var month = dateObj.getUTCMonth() + 1; //months from 1-12
var day = dateObj.getUTCDate();
var year = dateObj.getUTCFullYear();
let username;
function Upload(){
    history = useHistory()
    console.log(firebase.auth().currentUser)
    let uid;
   
 
    firebase.auth().onAuthStateChanged((user)=>{
        if(user){
           uid = user.uid
           firebase.database().ref("users/"+ uid + "/private/login credentials").on("value", snapshot=>{
               username = snapshot.val().userName
           })
        }else{
            history.push("/login")
        } 
    })
   
    const [showUpload, setShowUpload] = React.useState(true)
    const [showEdit, setShowEdit] = React.useState(false)
    function fileUpload(){
        file = document.getElementById("actual-btn").files[0]
        //document.getElementById("uc").style.display = "hidden";
        history.push("/upload/info")
        return (
            <Redirect to = "/upload/info" />
        )
      
    }
    useEffect(()=>{
        
    }, [])
   

    return(
        <>
            <Nav />
            {/* {showUpload ?  */}

              
            {/* : null } */}
            {/* {showEdit ? <Edit /> : null} */}
                
                    <Switch>
                        <Route  exact path = "/upload" >
                        <div className = "main" id = "upload-main">
                        <div className = "upload-container" id = "uc">
                            <div className = "upload-box"  onDrop = {fileUpload}>
                                <div className = "upload-main">
                                    <h2>Choose a song you want to upload</h2>
                                    <div className = "upload-button">
                                        <input type = "file" id ="actual-btn" hidden onChange = {fileUpload} accept = "audio/wav, audio/mp3"></input>
                                        <label for="actual-btn"><span>Upload file</span></label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                        </Route>
                        <Route  exact path = {"/upload/info"} exact component = {Edit}/>
                    </Switch>
             
     
        </>
    )
}

function Edit(){
  
    console.log(firebase)
    const [imagePreview, setImagePreview] = useState()
    function saveFile(event){
        console.log(event)
        event.target.disabled = true;
        let userID = firebase.auth().currentUser.uid
        let title = document.getElementById("title").value;
        let artist = document.getElementById("artist").value;
        let genre = document.getElementById("genre").value;
        let description = document.getElementById("description").value;
        let tags = document.getElementById("tags").value;
        let publicRad = document.getElementById("publicRad").checked;
        
        //let privateRad = document.getElementById("privateRad").checked;
        firebase.storage().ref("users"+userID+"/public/songs");
        var name = new Date() + '-' + file.name;
        var imageName = new Date() + "-" + image.name;
        let profilePic;
        firebase.database().ref("users/"+userID+"/public/profile/pic").on("value", (snapshot)=>{
            profilePic = snapshot.val().pfpUrl
        })
        if( title.indexOf(".") > -1 || title.indexOf("#") > -1 || title.indexOf("/") > -1 || title.indexOf("[") > -1 || title.indexOf("]") > -1){
            console.log("cannot use special charecter")
        }else{
            let imageURL;
            if (publicRad === true){
                    console.log("1st stage success")
                    const publicTask = firebase.storage().ref("public/"+"songs/"+genre+"/"+title).child(name).put(file);
                    const imageTask = firebase.storage().ref("public/"+"songs/"+genre+"/"+title).child(imageName).put(image);
                    imageTask.
                    then(snapshot => snapshot.ref.getDownloadURL())
                    .then(url=>{
                        console.log(url)
                        imageURL = url
                        console.log("2nd stage success")
                    })
                    .then(()=>{
                        console.log("3000000000nd stage success")
                        publicTask.on("state_changed", (snapshot)=>{
                            let progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                            let rProgress = Math.floor(progress)
                            console.log('Upload is ' + rProgress + '% done');
                        })
                        //const task = firebase.storage().ref("users/"+userID+"/public/"+"songs/"+title).child(name).put(file);
                    publicTask
                    .then(snapshot => snapshot.ref.getDownloadURL())
                    .then(url=>{
                        console.log(url)
                        firebase.storage().ref("preview").child(imageName).delete(image)
                        firebase.database().ref("users/"+userID+"/public/"+"songs/"+title).set({
                            "info":{
                                "userID": userID,
                                "title": title,
                                "artist": artist,
                                "genre": genre,
                                "tags": tags,
                                "description": description,
                                "uploader": username
                            },
                            "src":{
                                "aduioURL": url,
                                "imageURL": imageURL,
                                "pfpURL": profilePic,
                            },
                            "stats":{
                                "views": 0,
                            } ,
                            "recomend":{
                                "byGenre": 0,
                                "similarity": 0,
                                "mood": 0
                            }
                        }
                        )
                    .then(()=>{
                        firebase.database().ref("users/"+userID+"/public/"+"songs/"+title+"/date").set({
                            "month": month,
                            "day": day,
                            "year": year,
                            "totalDate": dayFunc(day, month, year)
                        })
                        firebase.database().ref("public/songs/"+"/"+title).set({
                           "info":{
                                "userID": userID,
                                "title": title,
                                "artist": artist,
                                "genre": genre,
                                "tags": tags,
                                "description": description,
                                "uploader": username
                            },
                            "src":{
                                "aduioURL": url,
                                "imageURL": imageURL,
                                "pfpURL": profilePic,
                            },
                            "stats":{
                                "views": 0,
                            } ,
                            "recomend":{
                                "byGenre": 0,
                                "similarity": 0,
                                "mood": 0
                            }
                        })
                    })
                        .then(()=>{
                            history.push("/")
                            })
                        })
                    })
                   
                 
            }else{
                let imageURL
                const privateTask = firebase.storage().ref("users/"+userID+"/private/"+"songs/").child(name).put(file);
                const imageTask = firebase.storage().ref("public/"+"songs/"+genre+"/"+title).child(imageName).put(image);
                    imageTask.
                    then(snapshot => snapshot.ref.getDownloadURL())
                    .then(url=>{
                        console.log(url)
                        imageURL = url
                    })
            privateTask.on("state_changed", (snapshot)=>{
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                let rProgress = Math.floor(progress)
                console.log('Upload is ' + rProgress + '% done');
            })
            privateTask
            .then(snapshot => snapshot.ref.getDownloadURL())
            .then(url=>{
                firebase.database().ref("users/"+userID+"/public/"+"songs/"+title).set({
                    "userID": userID,
                    "title": title,
                    "artist": artist,
                    "genre": genre,
                    "tags": tags,
                    "description": description,
                    "aduioURL": url,
                    "imageURL": imageURL
                   
                })
            })
            }     
        }        
    }
    function Cancel(){
        history.push("/")
    }
    function imageUpload(event){
        console.log(event.target)
        image = document.getElementById("album-btn").files[0];
        setImagePreview(URL.createObjectURL(event.target.files[0]))
    }
    return(
        <div className = "edit-upload">
        <div className = "edit-upload-box">
            <div className = "edit-upload-main">
                <div className = "pic-wrapper">
                    <div className = "pic-holder">
                        <img src = {imagePreview} width = "260" height = "260" alt="aimge-preview" id = "prev"></img>
                    </div>
                    <div className = "album-cover">
                        <input type = "file" id ="album-btn" hidden onChange = {imageUpload} accept = "image/jpeg, image/png"></input>
                        <label for="album-btn"><span>Upload image</span></label>
                    </div>
                </div>
                <div className = "song-form">
                    <div className = "song-title">
                        <span>Title</span>
                        <div className = "input-title">
                            <input id = "title" placeholder = "Name of your song..." className = "-input-form" defaultValue = {file.name} autoComplete = "off" />
                        </div>
                    </div>
                    <div className = "artist">
                        <span>Artist</span>
                        <div className = "input-artist">
                            <input id = "artist" placeholder = "Name of the artist..." className = "-input-form" autoComplete = "off" />
                        </div>
                    </div>
                    <div className = "Genre">
                        <span>Genre</span>
                        <div className = "input-gerne">
                            <input id = "genre" placeholder = "Name of your genre..." className = "-input-form" autoComplete = "off" />
                        </div>
                    </div>
                    <div className = "Description">
                        <span>Description</span>
                        <div className = "input-gerne">
                            <input id = "description" placeholder = "Describe your track.." className = "-input-form" autoComplete = "off" />
                        </div>
                    </div>
                    <div className = "Tags">
                        <span>Tags</span>
                        <div className = "input-tags">
                            <input id = "tags" placeholder = "add tags" className = "-input-form"  autoComplete = "off"/>
                        </div>
                    </div>
                    <div className = "privateOpt">
                        <span>Privacy?:</span>
                        <div className = "input-privacy">
                            <div className = "public">
                                <input type = "radio" id = "publicRad" name = "radio1" value = "public" />
                                <label for = "publicRad">Public</label> 
                            </div>
                            <div className = "private">
                                <input type = "radio" id = "privateRad" name = "radio1" value = "private" />
                                <label for = "privateRad">Private</label> 
                            </div>
                        </div>
                    </div>
                    <div className = "save-cancel">
                        <div className = "cancel">
                            <button onClick = {Cancel}>Cancel</button>
                        </div>
                        <div className = "save">
                            <button onClick = {saveFile}>Save</button>
                        </div>
                    </div>
                </div>
               
            </div>
         </div>
     </div>
    )
    
}
export default Upload