import React, { useEffect, useState } from "react";
import firebase from "firebase";
import dayFunc from "./functions/dayFunc.js";
import { Route, BrowserRouter as Router, Switch, useHistory, Redirect } from 'react-router-dom';
import "../styles/editProf.css"
import Moosix from "./functions/mossix-api"



function EditProfile(){
    let userId;
    firebase.auth().onAuthStateChanged((user)=>{
       userId = user.uid
    })

    const app = new Moosix(userId)
    document.title = "Edit Profile"
    const [file, setFile] = useState()

    const [x, setX] = useState(50)
    const [y, setY] = useState(50)
    const [zoom, setZoom] = useState(0)
    const [name, setName] = useState(null)
    const [firstName, setFirstName] = useState(null)
    const [lastName, setLastName] = useState(null)
    const [bio, setBio] = useState(null)
    const [imgSrc, setImgSrc] = useState(null)


    function image_handler(image){
        setFile(image[0])
        setImgSrc(URL.createObjectURL(image[0]))
    }
    // useEffect(()=>{
    //     app.checkName(name)
    // }, [name])

    function save(){
        const data = 
        {   
            data:{
                info:
                {
                    name: name,
                    firstName: firstName,
                    lastName: lastName,
                    bio: bio,
                },
              
                pfp: {
                    url: "",
                    x: x,
                    y: y,
                    zoom: zoom
                }
            },
            temp: {
                userId: userId
            } 
        }
       
        if(file)
        {
            app.update_profile_pic(file).then(()=>{
                app.update_profile(data)
            })
            
        }else{
            app.update_profile(data)
        }
        
        
    }
    function cancel(){

    }
    return(
        <div id = "edit-profile" className = "main">
            <div className = "edit-profile-container">
                <div className = "edit-profile-box">
                    <div className = "edit-head-wrapper">
                        <div className = "edit-head">
                            <span>Edit Your Profile</span>
                        </div>
                        <div className = "line" id = "edit-line" />
                    </div>
                    <div className = "profile-form-container">
                        <div className = "pfp-preview">
                            <div className = "pfp-preview-box">
                                <div className = "crop-container">
                                    <div className = "crop">
                                    </div>
                                    <img 
                                    src = {imgSrc} 
                                    alt = "image-preview" 
                                    id = "preview"
                                    style = 
                                    {
                                        {
                                            transform: `translate(${x-50}px, ${y-50}px) scale(${(parseInt(zoom)/100) + 1 })`,
                                           
                                        }
                                    }
                                    />
                                </div>
                               
                                <div className = "upload-pfp">
                                        <input type = "file" id ="upload-pfp-btn" hidden  onChange = {(event)=>image_handler(event.target.files)}></input>
                                        <label htmlFor="upload-pfp-btn"><span>Upload file</span></label>
                                </div>
                            </div>

                            <div className = "sliders">
                                    <div className = "slider-box">
                                        <div className = "slider-x">
                                            <input type = "range" value = {x} onChange = {(event)=>setX(event.target.value)}/>
                                        </div>
                                        <div className = "slider-y">
                                            <input type = "range" value = {y} onChange = {(event)=>setY(event.target.value)}/>
                                        </div>
                                        <div className = "slider-zoom">
                                            <input type = "range" value = {zoom} onChange = {(event)=>setZoom(event.target.value)}/>
                                        </div>
                                    </div>
                                </div>
                        </div>

                        <div className = "profile-form">
                            <div className = "display-name">
                                <div className = "form-label">
                                    <span>Display Name</span>
                                </div>
                                <div className = "display-name-input">
                                    <input 
                                    className = "-input-form" 
                                    type = "text" 
                                    placeholder = "Name..." 
                                    onKeyUp = {(event)=>setName(event.target.value)} 
                                    />

                                </div>
                            </div>
                            <div className = "first Name">
                                <div className = "form-label">
                                    <span>First Name</span>
                                </div>
                                <div className = "first-name-input">
                                    <input 
                                    className = "-input-form" 
                                    type = "text"  
                                    placeholder = "First Name..." 
                                    onKeyUp = {(event)=>setFirstName(event.target.value)} />
                                </div>
                            </div>
                            <div className = "last Name">
                                <div className = "form-label">
                                    <span>Last Name</span>
                                </div>
                                <div className = "last-name-input">
                                    <input 
                                    className = "-input-form" 
                                    type = "text"  
                                    placeholder = "Last Name..." 
                                    onKeyUp = {(event)=>setLastName(event.target.value)} />
                                </div>
                            </div>
                            <div className = "bio">
                                <div className = "form-label">
                                    <span>Bio</span>
                                </div>
                                <div className = "bio-input">
                                    <textarea 
                                    className = "-input-form" 
                                    type = "text"  
                                    placeholder = "describe yourself..." 
                                    onKeyUp = {(event)=>setBio(event.target.value)} />
                                </div>
                            </div>
                        </div>
                        <div className = "save-cancel">
                            <div className = "cancel">
                                <button onClick = {cancel}>Cancel</button>
                            </div>
                            <div className = "save">
                                <button onClick = {save}>Save</button>
                            </div>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EditProfile