import React, { Fragment, useEffect, useRef , useState}  from 'react';
import MusicPreview from "../widgets/musicPreview";
import Moosix from '../functions/mossix-api'
import firebase from "firebase"
let sliderVal2 = 0;


function Recomend(props){
    const app = new Moosix()
    const [recomend, setRecomend] = useState()
    const [slider2, setSlider2] = useState(sliderVal2)

    useEffect(() => {
        if(firebase.auth().currentUser){
            let uID = firebase.auth().currentUser.uid
            app.getRecommend(uID).then((data)=>setRecomend(data))
        }
    }, [firebase.auth().currentUser]);


    document.onscroll = function() {
        if(document.documentElement.scrollTop + window.innerHeight == document.documentElement.scrollHeight)
        {
            console.log("scrolled");
        }
    }

    function play(src){
        props.play(src)
    }
    function stop(){
        props.stop()
    }

    return(
        <div id="recomend-container" className="home-music-container ">
        <div className="musics-texts">
            <div className="musics-title">
                <h4>Recommended musics</h4>
            </div>
            <div className="musics-subtext">
                <span>Most recomended musics for you</span>
            </div>
        </div>
        <div className = "line" />
        <div className="preview-box-container" id = "recomend-box-container" style = {{ transform: `translate(${slider2}px, ${85}px)` }}>
            {recomend &&
            recomend.map((name, i) => {
                return (
                <Fragment key={i}>
                    <MusicPreview
                    name={name}
                    play={play}
                    userName={props.userName}
                    stop = {stop}
                    audio = {props.audio}
                    />
                </Fragment>
                );
            })}
        </div>
        <div className = "content-slider">
                <div  id = "next" className = "slider" onClick = {()=>{
                  sliderVal2 -= 225;
                  setSlider2(sliderVal2) 
                  }}>
                    <span class="material-icons">
                        keyboard_arrow_right
                    </span>
                </div>
                <div id = "previous" className = "slider" onClick = {()=>{
                    sliderVal2= sliderVal2 + 225;
                    console.log(sliderVal2)
                    setSlider2(sliderVal2) 
                }}>
                    <span class="material-icons">
                        keyboard_arrow_left
                    </span>
              </div>
          </div>
    </div>
    )
} 


export default Recomend