import React, { Fragment, useEffect, useRef , useState}  from 'react';
import MusicPreview from "../widgets/musicPreview";
import Moosix from '../functions/mossix-api'
let sliderVal2 = 0;


function Trending(props){
    const[trending, setTrending] = useState()
    const [slider2, setSlider2] = useState(sliderVal2)
    const app = new Moosix()
    useEffect(()=>{
      app.getTrending().then(data => setTrending(data))
    }, [])

    function play(src){
        props.play(src)
    }
    function stop(){
        props.stop()
    }

    return(
        
        <div id="trending-container" className="home-music-container ">
            <div className="musics-texts">
                <div className="musics-title">
                    <h4>Trending musics</h4>
                </div>
                <div className="musics-subtext">
                    <span>Most trending musics for you</span>
                </div>
            </div>

            <div className = "line" />
            
            <div className="preview-box-container" id = "trending-box-container" style = {{ transform: `translate(${slider2}px, ${85}px)` }}>
                {trending &&
                trending.map((name, i) => {
                    return (
                    <Fragment key={i}>
                        <MusicPreview
                        name={name}
                        play={play}
                        userName={props.userName}
                        stop = {stop}
                        audio = {props.audio}
                        />
                    </Fragment>
                    );
                })}
            </div>
            <div className = "content-slider">
                    <div  id = "next" className = "slider" onClick = {()=>{
                    sliderVal2 -= 225;
                    setSlider2(sliderVal2) 
                    }}>
                        <span className="material-icons">
                            keyboard_arrow_right
                        </span>
                    </div>

                    <div id = "previous" className = "slider" onClick = {()=>{
                        sliderVal2= sliderVal2 + 225;
                        console.log(sliderVal2)
                        setSlider2(sliderVal2) 
                    }}>
                        <span class="material-icons">
                            keyboard_arrow_left
                        </span>
                </div>
                
            </div>

    </div>
    )
}

export default Trending