import React, { Fragment, useEffect, useRef } from "react";

import firebase from "firebase"
import { useState } from "react"
import { NavLink, Redirect } from "react-router-dom";
import Like from "./like"
import Hover1 from "./Hover1.mp3"
function MusicPreview(props){
    const sfxRef = useRef();
    const [btn_hidden, setBtn_hidden] = useState(true)
    let sfx;
    let name = props.name;
    let uID = props.uID
    let currentUserName = props.userName
    const audio = props.audio
    useEffect(()=>{
        if(audio.src != name.src.aduioURL || audio.paused){
            setBtn_hidden(true)
        }
        if(audio.src == name.src.aduioURL && !audio.paused){
            setBtn_hidden(false)
        }
    }, [audio.src, name.src.aduioURL, audio.paused])

    function play(event){
        setBtn_hidden(false)
        props.play(name)
    }
    function stop(event){
        setBtn_hidden(true)
        props.stop(event)
    }
   
    return(
         <div className = "music-preview-box" >
            <audio id = "sfx" src = {Hover1} ref = {sfxRef} type="audio/mpeg"/>
            <NavLink to = {`/listen?m=${"idk"}&uid=${"idk"}`}>
                <div className = "user-musics-content" style ={ { backgroundImage: `url("${name.src.imageURL}")` } } />
            </NavLink>
           <div  className = "-button">
                    {btn_hidden?
                    <i className="material-icons"onClick = {play} value = {name.info.title} id = "play_hover">
                        play_arrow
                    </i>:
                    <i onClick = {stop} className = "material-icons">pause</i>
                    }
            </div>  
            <div id = "user-song-info">
            <div class = "user-song-title">
                    <span>{name.info.artist} - {name.info.title}</span>
            </div> 
                <div class = "user-song-user">
                    <div class = "mini-pfp">
                        <img src = {props.pfp} width = "30" height = "30" />
                    </div>
                    <NavLink to ={"/u/"+name.info.userID+"/music"}><span>{name.info.uploader}</span></NavLink>
                </div>

                <div className = "stats">
                    <div class = "views">
                        <i class="material-icons">
                            play_arrow
                        </i>
                        <span>{name.stats.views}</span>
                    </div>
                    <Like name = {name.info.title} uID = {name.info.userID} userName = {currentUserName} music = {name}/>
                </div>
                
            </div>
    </div>
    )
}

export default MusicPreview
