import React, { Fragment, useEffect, useRef,  } from "react";
import firebase from "firebase";
import fire from "./firebase"
import { Route, BrowserRouter as Router, Switch, Redirect, useHistory, useLocation } from 'react-router-dom';
import Header from "./header"
import Nav from "./nav"
import AudioPlayer from "./audioplayer"
import { useState } from "react"
import MusicPreview from "./widgets/musicPreview"
import Moosix from "./functions/mossix-api"

const app = new Moosix()
function Result(props){
    const [currentUserName, setCurrrentUserName] = useState()
    const audioRef = useRef();
    const audioPlayer = audioRef.current;
    const [user, setUser] = useState(null)
    const [musics, setMusics] = useState([])
    const [progVal, setProgVal] = useState("0%")
    const history = useHistory();
    const [uID, setUID] = useState();
    let query = useQuery();

    function useQuery() {
        return new URLSearchParams(useLocation().search);
    }

    const search_query = query.get("search")
    console.log(search_query)  
    useEffect(()=>{
        firebase.auth().onAuthStateChanged((currentUser)=>{
            firebase.database().ref("users/"+currentUser.uid+"/praivate/login credentials").on("value", (snapshot)=>{
                if(snapshot.val()){
                    setCurrrentUserName(snapshot.val().userName)
                }
            })
        })
    }, [])

    useEffect(()=>{
        app.searchQ(search_query).then((musics)=>setMusics(musics)  )
    }, [search_query])
    
    function play(name){
        props.play(name)
    }

    function stop(){
        props.stop()
    }

    

    return(
        <>
             
        <div id = "home" className ="main">
            <div className = "music-content-container" id = "home-musics">
                <div className = "music-box">
                    <div className = "user-musics">
                        {musics && musics.map((name, i)=>{   
                            return(
                                <Fragment key = {i}>
                                    <MusicPreview 
                                        name = {name} 
                                        play = {play}
                                        stop = {stop} 
                                        uID = {name.userID} 
                                        userName = {currentUserName} 
                                        pfp= {name.pfpURL} 
                                        user = {name.artist}
                                        audio = {props.audio}
                                    />
                                    </Fragment>
                                )
                        })}
                        {
                            musics.length == 0
                            ? 
                            <span>no results found</span>
                            :
                            null
                        }
                    </div>    
                </div>
            </div>  
        </div>
        
        </>
    )
}
export default Result