import React, { useEffect } from "react";

import firebase from "firebase";
import Popular from "./sub-home/Popular"
import Header from "./header";
import Nav from "./nav";
import AudioPlayer from "./audioplayer";
import { useState } from "react";
import Recomend from "./sub-home/Recomend"
import Trending from "./sub-home/Trending"

const audio = new Audio()



function Home(props) {
    let name1;
    document.title = "Moosix"
    const db = firebase.database()
    const [currentUserName, setCurrrentUserName] = useState();
    const [progVal, setProgVal] = useState("0%");
    const auth = firebase.auth()

    useEffect(() => {
        auth.onAuthStateChanged((currentUser) => {
        if (currentUser) {
            firebase
            .database()
            .ref("users/" + currentUser.uid + "/private/login credentials")
            .on("value", (snapshot) => {
                setCurrrentUserName(snapshot.val().userName);
            });
        }
        });
    }, []);

  

 
    let duration = 0;
    function play(src) {
        props.play(src)
    }


    function stop(event) {
       props.stop()
    }

 
    return (
        <>
            <div id="home" className="main">
                <div className="music-content-container" id="home-musics">
                    <div className="music-box" id = "home-box">
                        <Popular play = {play} stop = {stop} userName = {currentUserName} audio = {props.audio}/>
                        <Trending play = {play} stop = {stop} userName = {currentUserName} audio = {props.audio}/>
                        <Recomend  play = {play} stop = {stop} userName = {currentUserName} audio = {props.audio}/>
                    </div>
                </div>
            </div>
        </>
    );
}
export default Home;
