import React, { Fragment, useEffect, useRef } from "react";
import MusicPreview from "./widgets/musicPreview"

function MusicContent(props){
    let musics = props.musics;
    let name = props.name;
    let uID = props.uID;
    let currentUserName = props.userName
    let pfp = props.pfp
    let user = props.user
    function play(src){
        props.play(src)
    }
    function stop(){
        console.log("yes stop")
        props.stop()
    }
   
    return(
                <div class = "music-content-container">
                    <div class = "music-box">
                        <div class = "user-musics">
                                {musics && musics.map((name, i)=>{   
                                    return(
                                        <Fragment key = {i}>
                                                <MusicPreview 
                                                    name = {name} 
                                                    play = {play} 
                                                    uID = {uID} 
                                                    userName = {currentUserName} 
                                                    pfp= {pfp} 
                                                    user = {user}
                                                    stop = {stop}
                                                    audio = {props.audio}
                                                />
                                        </Fragment>
                                    )
                                })}
                        </div>
                    </div>
                </div>  
    )
}

export default MusicContent