import firebase from "firebase"

class Moosix
{
    constructor(uid){
        this.uid = uid
    }

    async getPopular()
    {
        const res = await fetch("/api/home/popular", { mode: "cors" })
        const response = await res.json()
        return response
    }

    async getTrending()
    {
        const res = await fetch("/api/home/trending", { mode: "cors" })
        const response = await res.json()
        return response
        
    }

    async getRecommend(userId)
    {
        const res = await fetch("/api/home/recomended?uid="+userId)
        const response = await res.json()
        return response
    }

    async getUserSongs(userId)
    {
        const res = await fetch ("/api/u/musics?uid="+userId)
        const response = await res.json()
        return response
    }

    async getUserData(userId)
    {
        const res = await fetch("/api/u/data?uid="+userId)
        const response = await res.json()
        return response
    }

    async searchQ(search)
    {
        if(search != "" && search != null)
        {
            const res = await fetch("/api/home/result?search="+search, {mode: 'cors'})
            const response = await res.json()
            return response
        }
    }

    checkName(name)
    {
        const url = "/api/profile/checkname"
        if(name != null || name != ""){
            return this.postData(url, {"name": name})
        }
    }

    update_profile(data)
    {
        if(this.url){
            data.data.pfp.url = this.url
        }
        console.log(data)
        const url = "/api/profile/update"
        this.postData(url, {"update": data})
    }

    async update_profile_pic(pic)
    {
        const st = firebase.storage()
        await st.ref("/users/profile/pfp").child(pic.name).put(pic)
                .then((snapshot)=>snapshot.ref.getDownloadURL())
                .then(url=>this.url = url)
                .then(()=>console.log("completed"))
    }
    
    sendLike(liker_uid, u_uid, title, music)
    {
        const url = "/api/music/like/send"
        const data = 
        {
            likedBy: liker_uid,
            likedOf: u_uid,
            song_name: title,
            song: music
        }
        this.postData(url, data)
    }





    async  postData(url = '', data = {}) {
        // Default options are marked with *
        const response = await fetch(url, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors', // no-cors, *cors, same-origin
          cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
          credentials: 'same-origin', // include, *same-origin, omit
          headers: {
            'Content-Type': 'application/json',
          },
          redirect: 'follow', // manual, *follow, error
          referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
          body: JSON.stringify(data) // body data type must match "Content-Type" header
        });
        return response.json(); // parses JSON response into native JavaScript objects
    }
}
export default Moosix