import React, { Fragment, useEffect, useRef } from "react";
import Moosix from "../functions/mossix-api.js"
import firebase from "firebase"
import { useState } from "react"

const app = new Moosix()
function Like(props){
    let cUID;
    const uID = props.uID //sets userId
    let music = props.music
    let u_data;
    let cu_data;
    if(firebase.auth().currentUser){
        cUID=firebase.auth().currentUser.uid
    }
    var title = props.name //sets tractk title
    let userName = props.userName //sets userName
    let likeCheck; //only true or false
    const [likeNumber, setLikeNumber] = useState(music.stats.likes)
    const [like, setLike] = useState()
    const [counter, setCounter] = useState() 
    const hasLiked = music.widgetInfo && music.widgetInfo.likes[cUID] != undefined
        useEffect(()=>{
           
            //goes to firebase and check if the value is true or undefined
            if(hasLiked){
                setLike(true)
            }else{
                setLike(false)
            }
        }, [userName])
        
        function sendLike(){
            app.sendLike(cUID, uID, title, music)
          
            if(like){
                setLikeNumber(likeNumber - 1)
                setLike(false)
            }else{
                setLike(true)
                setLikeNumber(likeNumber+ 1)
            }
        }
    return(
        <div class = "like">
            <i class="material-icons" onClick = {sendLike} >{like ? <p>favorite</p> : <p>favorite_border</p> }</i>
            <span>{likeNumber}</span>
        </div>
    )
}
export default Like