import React from "react"
import { NavLink } from "react-router-dom";
import { useState, useEffect, useRef } from "react"
import firebase from "firebase";
import fire from "./firebase"
import { useHistory } from "react"


function AudioPlayer(props){
    const audioRef = useRef();
    const audioPlayer = audioRef.current
    const [audioSrc, setAudioSrc] = useState()
    const [progVal, setProgVal] = useState(0)
    const [volume, setVolume]  = useState(1)
    let playA = props.play;
    
    useEffect(() => {
       setProgVal(props.progVal)
      },[props.progVal])

    function stop(e){
        props.stop()
    }

    function play(){
        props.playAudio()
    }

    useEffect(()=>{
        props.volumeHandler(volume)
    }, [volume])
    
    

    
    return(
            <div className = "audio-wrapper">
                <div className = "audio-container">
                    <div className = "audio-box">
                        <div className = "play_btn-container">
                            {
                                props.audio.paused? 
                                <span onClick = {play} id = "play_btn" className = "material-icons">play_arrow</span>
                                :
                                <span onClick = {stop} id = "play_btn" className = "material-icons">pause</span>
                            }
                        </div>
                        <div className = "progress_div">
                            <div class = "progress_holder" id= "prog_holder">
                                <div id = "progress_meter" style = {{width: progVal + "%"}}></div>
                            </div>  
                        </div>
                        <div className = "volume">
                            <div className = "volume-i">
                                <span class="material-icons">
                                    volume_up
                                </span>
                            </div>
                            <div className = "volume_slider">
                                <input type = "range" onChange = {(event)=>setVolume(event.target.value/100)} value = {volume*100}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    )
}

export default AudioPlayer