import { NavLink } from "react-router-dom";
import React from "react";
import firebase from "firebase/app"
import "./firebase"
import { Route, BrowserRouter as Router, Switch, Redirect, useHistory } from 'react-router-dom';
import fire from "./firebase";
import { useState } from "react"
var check = false;
function Login(props){
   let email;
   let password;
   let userName;  
    const history = useHistory();
    const [errorMessage, setErrorMessage] = useState()
    function loginE(props){
        email = document.getElementById("email").value;//gets type value of user email
        password = document.getElementById("password").value;//gets type value of user password
        //sends email to database to test
        async function send(uEmail, Password){
            await firebase.auth().signInWithEmailAndPassword(uEmail, Password).then(()=>{
                userName = firebase.auth().currentUser.uid; //sets user id to variable
                history.push("/") // if user logged in, redirects to home page
            }).catch((err)=>{ //incorrect password
                console.log("incorrect")
                setErrorMessage(err.message)
            })
            firebase.auth().onAuthStateChanged((user)=>{
                if(user){
                    console.log(user);
                }else{
                    console.log("nop")
                }
            })
            
        }
        send(email, password)
    }
    
    return(
        
        <div id = "login" className ="main-login">
              
            <div className = "form-box">
                <div className = "form-container">
                    <div className = "inputHolder">
                        <div className = "logo-form">
                            <h1>Logo</h1>
                        </div>
                        <div className = "input-container">
                            
                            <div className = "email-holder">
                                <input placeholder = "email adress" id = "email"
                                className = "-input" />
                            </div>
                            <div className = "password-holder">
                                <input placeholder = "password"  id = "password" type = "password" className = "-input"/>
                            </div>
                            <div className = "btn-holder">
                                <button onClick = {loginE}><span><b>Login</b></span></button>
                            </div>
                        </div>
                        
                    </div>
                    <div class = "link-signup">
                        <p>{errorMessage}</p>
                        <p>Havent created account? plz <NavLink to = "./signup"> click here</NavLink></p>   
                    </div>
                </div>
            </div>
           
        </div>
        
    )
}
export default Login;