import React, { Fragment, useEffect, useRef } from "react";
import fire from "./firebase";
import firebase from "firebase"
import { useState } from "react"
import Header from "./header"
import Nav from "./nav"
import { NavLink, Redirect, Route, useHistory } from "react-router-dom";
import AudioPlayer from "./audioplayer"
import MusicContent from "./musicContent"
import { BrowserRouter as Router, Switch } from 'react-router-dom';
import Moosix from "./functions/mossix-api"
const audio = new Audio()
const app = new Moosix()
const Music = (props) => {
    const [x, setX] = useState()
    const [y, setY] = useState()
    const [zoom, setZoom] = useState()
    const [user_data, setUser_data] = useState()
    const [musics, setMusics] = useState([])
    const [user, setUser] = useState(null)
    const [pfp, setPfp] = useState(null)
    const [currentUserName, setCurrrentUserName] = useState()
    const { params: { uID } } = props.match;
    var tracks = [];
    const u_profile = firebase.database().ref("users/" + uID + "/public/profile")
    const [progVal, setProgVal] = useState("0%")
    //profile pictures and all
  
    useEffect(() => {
        app.getUserSongs(uID).then((song)=>setMusics(song))
        app.getUserData(uID).then((data)=>setUser_data(data))
        firebase.auth().onAuthStateChanged((currentUser) => {
            app.getUserData( currentUser.uid).then(data=>setCurrrentUserName(data.public.profile.info.name))
        })
    }, [uID])
    useEffect(()=>{
            if(user_data){
                setUser(user_data.public.profile.info.name)
                setPfp(user_data.public.profile.pfp.url)
                setX(user_data.public.profile.pfp.x)
                setY(user_data.public.profile.pfp.y)
                setZoom(user_data.public.profile.pfp.zoom)
            }
    }, [user_data])
    

    //stores musics data in array for rendering


    //play function no bbugs here
    function play(src) {
        console.log(props)
        props.play(src)
     }
  
    //no bugs here
   

    //no bugs here
    function stop() {
        props.stop()
    }
  
    
    return (
        <>

            <div id="profile" className="main">
                <div class="profile-container">
                    <div className="cover-pic">
                        <img src="" />
                        
                        <div className="User-Name">
                            <h1>{user}</h1>
                        </div>
                    </div>
                    <div className="pfp">
                        <div className = "pfp-container">
                            <pfp className = "pfp-box">
                                <img 
                                src={pfp}  
                                style = 
                                {
                                    {
                                        transform: `translate(${x-50}px, ${y-50}px) scale(${(parseInt(zoom)/100) + 1 })`,
                                        
                                    }
                                }
                                />
                            </pfp>
                        </div>
                            
                        </div>
                    <div class="tabsContainer">
                        <div class="tabs-holder">
                            <NavLink to={"/u/" + uID + "/music"} activeClassName="Music-Nav-Active">
                                <button><span>Music</span></button>
                            </NavLink>
                            <NavLink to={"/u/" + uID + "/playlist"} activeClassName="PlayList-Nav-Active">
                                <button><span>Playlist</span></button>
                            </NavLink>
                            <button><span>About</span></button>
                        </div>
                    </div>
                </div>
                <Redirect to = {"/u/" + uID + "/music"} />
                <Switch>
                    <Route exact path={"/u/" + uID + "/music"}>
                        <MusicContent
                            musics={musics}
                            play={play}
                            uID={uID}
                            userName={currentUserName}
                            pfp={pfp}
                            user={user}
                            stop = {stop}
                            audio = {props.audio}
                        />
                    </Route>
                    <Route exact path={"/u/" + uID + "/playlist"}>
                    </Route>
                </Switch>
            </div>
        </>
    )
}
export default Music




